<template>
	<section>
		<aside class="aside" v-show="show">
			<div class="logo">
				<span @click="toggleShow"></span>
			</div>
			<transition name="slide-fade">
				<nav v-show="navShow">
					<ul class="nav-list">
						<li>
							<router-link to="/">全球旅拍</router-link>
						</li>
						<li>
							<router-link to="/" class="letter-spacing-2">首页</router-link>
						</li>
						<li>
							<router-link to="/">国内摄影</router-link>
						</li>
						<li>
							<router-link to="/">海外摄影</router-link>
						</li>
						<li>
							<router-link to="/" class="letter-spacing-2">旅游</router-link>
						</li>
					</ul>
				</nav>
			</transition>
		</aside>
	</section>
</template>
<script>
	export default {
		name: 'Aside',
		props: {
			show: {
				type: Boolean,
				default: false,
			}
		},
		data () {
			return {
				navShow: true,
			}
		},
		methods: {
			toggleShow() {
				// this.navShow = !this.navShow;
				this.$router.push('/')
			},
		}
	}
</script>
<style scoped lang="scss">
	.aside {
		position: fixed;
		top: 150px;
		left: 0;
		z-index: 999;
		width: 120px;
		font-family: 'Microsoft Yahei';
		// @media (max-width: 640px) {
		// 	display: none;
		// }
		.logo {
			height: 60px;
			text-align: center;
			vertical-align: middle;
			background: #c60c1a;
			span {
				display: inline-block;
				width: 40px;
				height: 40px;
				margin-top: 10px;
				cursor: pointer;
				background: url(../assets/img/aside_logo.png) no-repeat center;
				background-size: cover;
			}
		}
	}
	.nav-list {
		text-align: center;
		background: #000;
		li {
			text-align: center;
			transition: all .5s;
			&:nth-of-type(even) {
				background: #333;
			}
			&:hover, &.active {
				background: #c60c1a;
			}
			a {
				display: inline-block;
				width: 100%;
				height: 40px;
				line-height: 40px;
				color: #fff;
				font-size: 16px;
				
			}
			span {
				color: #337ab7;
				cursor: pointer;
			}
		}
	}
	.letter-spacing-2 {
		letter-spacing: 1.5em;
		text-indent: 1.5em;
	}
	.slide-fade-enter-active {
    transition: all .3s ease;
	}
	.slide-fade-leave-active {
	  transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	.slide-fade-enter, .slide-fade-leave-to {
	  transform: translateY(-10px);
	  opacity: 0;
	}
</style>


