<template>
	<div id="layout">
    <HeaderComp :userInfo="userInfo"></HeaderComp>
    <keep-alive>
      <MainComp :userInfo="userInfo"></MainComp>
    </keep-alive>
	</div>
</template>
<script>
import HeaderComp from './HeaderBar'
import MainComp from './Main'
import { getMyInfo } from '@/api'
export default {
  name: 'layout',
  data() {
    return {
      userInfo: null
    }
  },
  components: {
    HeaderComp,
    MainComp
  },
  methods: {
    getUserInfo() {
      getMyInfo().then(res => {
        if(res.data.code === '0001') {
          this.userInfo = res.data.result.userInfo
        } else {
          this.userInfo = null;
        }
      }).catch(err => {
        console.log(err)
        this.$catchError(err)
      })
    }
  },
  mounted() {
    this.getUserInfo()
  }
}
</script>



