<template>
	<header class="header">
		<div class="container">
			<div class="header-left clearfix">
				<div class="header-logo">
					<router-link to="/" class="logo">logo</router-link>
				</div>
				<ul class="header-nav">
					<li><router-link to="/">首页</router-link></li>
					<li><router-link to="/">全球旅拍</router-link></li>
					<li><router-link to="/">国内摄影</router-link></li>
					<li><router-link to="/">国外摄影</router-link></li>
					<li><router-link to="/">旅游</router-link></li>
				</ul>
			</div>
			<div class="header-right clearfix">
				<div class="searchbar">
					<input class="search-input" placeholder="悦视觉全球摄影"></input>
					<i class="el-icon-search"></i>
				</div>
				<div class="shopping-cart">
					<i class="icon cart"></i>
				</div>
				<div v-if="userInfo" class="user-link">
					<i class="icon user"></i>
					<router-link to="/">{{ userInfo.name }}</router-link>
					<em>|</em>
					<span @click="handleExit">退出</span>
				</div>
				<div v-else class="user-link">
					<router-link to="/login">登录</router-link>
					<em>|</em>
					<router-link to="/">注册</router-link>
				</div>
			</div>
		</div>
	</header>
</template>
<script>
	import { requestExit } from '@/api'
	export default {
		data() {
			return {

			}
		},
		props: ['userInfo'],
		methods: {
			handleExit() {
				this.$confirm('确定退出本次登录', '提示', {type: 'warning'}).then(() => {
						requestExit().then(res => {
							if(res.data.code === '0001') {
								this.$router.replace( '/login')
								// this.$message.success('退出成功')
							} else {
								this.$message.error(res.data.message)
							}
						})
				}).catch(err => {
					console.log(err)
					// this.$message('已取消操作')
				})
			}
		}
	}
</script>
<style scoped lang="scss">
	.header {
		position: fixed;
		top: 0;
		z-index: 9999;
		width: 100%;
		padding: 10px;
		background: rgba(0,0,0,.7);
		.header-left {
			float: left;
		}
		.header-right {
			float: right;
			height: 47px;
			padding-top: 8px;
		}
		.header-logo {
			float: left;
			.logo {
				display: inline-block;
				width: 118px;
				height: 43px;
				margin-top: 4px;
				text-indent: -2000em;
				background: url(../assets/img/logo.png) no-repeat center;
				background-size: cover;
			}
		}
		.header-nav {
			float: left;
			padding-left: 15px;
			line-height: 47px;
			li {
				float: left;
				margin-left: 25px;
				font-size: 18px;
				a {
					color: #fff;
					transition: all .3s;
					&:hover {
  					padding-bottom: 6px;
						border-bottom: 2px #EFE701 solid;
					}
				}
			}
		}
		.searchbar {
			float: left;
			width: 260px;
			height: 32px;
			line-height: 32px;
			padding: 0 12px;
			border-radius: 14px;
			background: #fff;
			.search-input {
				width: 200px;
				font-size: 16px;
				border: none;
			}
			.el-icon-search {
				position: relative;
				right: -15px;
				cursor: pointer;
				color: #999;
			}
		}
		.shopping-cart {
			float: left;
			margin: 0 40px;
			font-size: 24px;
			color: #fff;
			.cart {
			  width: 29px;
			  height: 20px;
			  cursor: pointer;
				background-position: -186px 0;
				&:hover {
					background-position: -383px -1px;
				}
			}
		}
		.user-link {
			float: left;
			margin-top: 6px;
			color: #fff;
			font-size: 16px;
			cursor: pointer;
			.user {
		    width: 21px;
		    height: 20px;
		    vertical-align: bottom;
		    margin-right: 5px;
				background-position: -215px 0;
			}
			a {
				color: #fff;
			}
			em {
				margin: 0 8px;
			}
		}
	}
</style>


