<template>
	<div class="toolbar">
		<div class="toolbar-wrap">
			<div class="tbar-tab user">
				<router-link to="/">
					<span class="tab-icon"><i class="icon tbar-icon-user"></i></span>
					<em class="tab-text">会员权益</em>
				</router-link>
			</div>
			<div class="tbar-tab cart">
				<router-link to="/">
					<span class="tab-icon"><i class="icon tbar-icon-cart"></i></span>
					<em class="tab-text">购物车</em>
				</router-link>
			</div>
			<div class="tbar-tab tbar-icon-order">
				<router-link to="/">
					<span class="tab-icon"><i class="icon tbar-icon-order"></i></span>
					<em class="tab-text">我的订单</em>
				</router-link>
			</div>
			<div class="tbar-tab message">
				<router-link to="/">
					<span class="tab-icon"><i class="icon tbar-icon-message"></i></span>
					<em class="tab-text">我的消息</em>
				</router-link>
			</div>
			<div class="tbar-tab favorite">
				<router-link to="/">
					<span class="tab-icon"><i class="icon tbar-icon-favorite"></i></span>
					<em class="tab-text">我的收藏</em>
				</router-link>
			</div>
			<div class="tbar-tab service">
				<router-link to="/">
					<span class="tab-icon"><i class="icon tbar-icon-service"></i></span>
					<em class="tab-text">联系客服</em>
				</router-link>
			</div>
			<div class="tbar-tab back-top" @click="toTop">
				<span class="tab-icon"><i class="el-icon-arrow-up"></i></span>
				<em class="tab-text">返回顶部</em>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {

			}
		},
		methods: {
			toTop () {
	    	let timer = null;
	      let gotoTop = () => {
	        let currentPosition = document.documentElement.scrollTop || document.body.scrollTop;
	        currentPosition -= currentPosition*0.2;
	        if (currentPosition > 0) {
	          window.scrollTo(0, currentPosition);
	        } else {
	          window.scrollTo(0, 0);
	          clearInterval(timer);
	          timer = null;
	        }
	      }
	      timer = setInterval(gotoTop, 1)
	    }
		}
	}
</script>
<style scoped lang="scss">
	.toolbar {
		position: fixed;
		top: 0;
		right: -35px;
		bottom: 0;
		z-index: 9999;
	  width: 35px;
		height: 100%;
		margin: 0;
	  background: #000;
	  opacity: 0;
	  transition: all .5s;
	}
	.toolbar-wrap {
		position: relative;
  	top: 20%;
  	.tbar-tab {
  		width: 35px;
  		height: 35px;
  		margin: 10px 0;
  		line-height: 35px;
  		color: #fff;
  		text-align: center;
  		position: relative;
  		&.back-top {
  			margin-top: 50px;
				cursor: pointer;
  		}
  		.tab-icon {
  			display: inline-block;
  			width: 35px;
  			height: 35px;
  			line-height: 35px;
  			background: #000;
  			position: relative;
		  	z-index: 2;
  		}
		  .icon { 
		  	margin-top: 7px;
		  	// vertical-align: middle;
		  	&.tbar-icon-user {
		  		width: 21px;
			    height: 20px;
			    background-position: -215px 0;
		  	}
		  	&.tbar-icon-cart {
			    width: 29px;
			    height: 20px;
		  		background-position: -383px -1px;
		  	}
	  		&.tbar-icon-order {
					width: 18px;
			    height: 23px;
					background-position: -412px 0;
	  		}
	  		&.tbar-icon-message {
			    width: 21px;
			    height: 23px;
					background-position: -430px 0;
		  	}
		  	&.tbar-icon-favorite {
			    width: 23px;
			    height: 23px;
				  background-position: -451px 0;
		  	}
		  	&.tbar-icon-service {
			    width: 20px;
			    height: 23px;
				  background-position: -474px 0;
		  	}
	  	}
  		.tab-text {
				position: absolute;
				top: 0;
				right: -65px;
				z-index: 1;
				width: 90px;
				color: #fff;
				opacity: 0;
				background: #e50110;
				transition: all .5s;
			}
  		&:hover {
  			.tab-icon {
  				background: #e50110;
  			}
  			.tab-text {
  				right: 35px;
  				opacity: 1;
  			}
  		}
  		&.cart:hover {
  			.icon {
  				&.tbar-icon-cart {
				    width: 29px;
				    height: 23px;
			  		background-position: -186px 0;
			  	}
  			}
  		}
  	}
  }
</style>


